	module.exports = {"data":{"postBySlug":{"html":"<p>Pourquoi un dépôt de connaissances sur les dojos craft ?</p>\n<p>Le <a href=\"http://manifesto.softwarecraftsmanship.org/#/fr-fr\">manifeste Craft</a> parle de communauté de professionnels du développement logiciel et de relever le niveau.</p>\n<p>Le métier de développeur logiciel est récent comparé à la construction de bâtiment ou à la météorologie.\nVous connaissez la fiabilité de ceux deux métiers.\nVous savez que les logiciels veillissent mal ?\nVous connaissez les problèmes de logiciels qui perdent en flexibilité et en productivité jusqu'à la non rentabilité ?\n<strong>Le mouvement du développement artisanal de logiciels cherche, essaye et contribue à améliorer les valeurs, principes et pratiques qui relèvent le niveau dans leur métier.</strong></p>\n<p>Comme ces recherches ne sont utiles que si elles sont diffusées et appliquées, nous proposons des évènements où l'on essaye et on s'entraîne.\nCes évènements se déroulent dans des salles de formation ou de réunion que l'on nomme des \"coding dojos\".\nLes exercices proposés sont donc des \"katas\" de programmation (le plus souvent).\nC'est un contexte sans mise en prod, sans \"évaluation du management\" ni autre pression exterieure où la seule contrainte est de s'approprier une nouvelle technique.</p>\n<p>Ce contenu accompagnera les tech lead, scrum master et coach dans l'aventure du partage de bonnes pratiques et de montée en savoir-faire craft.</p>","timeToRead":1,"excerpt":"Pourquoi un dépôt de connaissances sur les dojos craft ? Le  manifeste Craft  parle de communauté de professionnels du développement…","frontmatter":{"title":"Introduction","cover":null,"date":null,"category":null,"tags":null}},"tableOfContents":{"chapters":[{"title":"2. Katas","entries":[{"entry":{"id":"C:/Users/lenovo_12/Documents/arolla/raise-the-dojo/gatsby-generator/static-site/content/lessons/whitebook.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/notion-de-kata"},"frontmatter":{"title":"Notion de Kata"}}}},{"entry":{"id":"C:/Users/lenovo_12/Documents/arolla/raise-the-dojo/gatsby-generator/static-site/content/lessons/rental_movie.md absPath of file","childMarkdownRemark":{"fields":{"slug":"/rental-movie"},"frontmatter":{"title":"Rental-Movie"}}}}]}]}},"pathContext":{"slug":"/introduction"}}

